<template>
  <div class="product">
    <div class="leftTabs">
      <el-menu :default-active="active"  class="el-menu-vertical-demo"  @select="handleSelect">
      <el-menu-item index="1">
        <i class="el-icon-menu"></i>
        <span slot="title">公司简介</span>
      </el-menu-item>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">商务合作</span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-document"></i>
        <span slot="title">联系我们</span>
      </el-menu-item>
    </el-menu>
    </div>
    <div class="rightContent">
      <div v-if="active == '1'">
        <h3><div>西安千将云信息科技有限公司，致力于导航诱骗装备的设计研发、生产制造、组装测试及销售。</div><div>同时，公司提供低空安全问题系统解决方案公司现有36人，其中核心研发人员占比60%，均为硕士及以上学历在电子对抗、信号及图像分析等技术领域有多年经验积累。</div><div>目前已自主研发了基于导航诱骗技术的“蓝星系列无人机反制装备，包含自动跟踪、察打一体、车载移动等多种款式，拥有发明专利和多项软件著作权。</div>
<div>产品均通过了公安部第一研究所的检测，同时参加过军内举办的多次比测。</div><div>和中国电科、航天科技等系统的多个科研单位以及企业进行了深度合作。产品已经在军用、警用及民用等多场景中得到了实战的检验。</div></h3>
      </div>
      <div v-else-if="active == '2'">
        <img :src="src2" />
      </div>
      <div v-else-if="active == '3'">
         <img :src="src3" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: '1',
      src1: require('../assets/img/product1.png'),
      src2: require('../assets/img/product2.png'),
      src3: require('../assets/img/product3.png')
    }
  },
  watch: {
    $route(to){
      window.console.log(to)
      if (to.name == 'goin') {
        this.active = to.params.id
      }
      
    }
  },
  mounted() {
    window.console.log(this.$route.params.id)
    this.active = this.$route.params.id
  },
  methods: {
    handleSelect(key) {
      this.$router.push({name:'goin',params: {id:key}})
    },
  }
};
</script>

<style lang="scss" scoped>
.product{
  overflow: hidden;
  width: 100%;
  height: 100vh;
}
.leftTabs{
  float: left;
  width: 30%;
  ul{
    margin-left: 50%;
    width: 50%;
    text-align: left;
  }
  .el-menu {
    border: none !important;
  }
}
.rightContent{
  float: right;
  width: 70%;
  div {
    width: 100%;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
